"use client";

import { useEffect, useState } from "react";
// Import your API functions
import { fetchCatalog, sendQuery, ParseResponse } from "@/app/api/backend";
// Import your state and prompt logic
import { AppState, CategoryState, generateLLMPrompt } from "@/app/utils/prompt";

// Import all your UI components
import Chip from "@/components/Chip"; // Uses the one in src/components/
import CategoryCard from "@/app/components/CategoryCard"; // Uses the one in src/app/components/
import CustomQueryBox from "@/app/components/CustomQueryBox";
import LoadingView from "@/app/components/LoadingView";
import ResultView from "@/app/components/ResultView";
import Navbar from "@/app/components/Navbar";

// Main page component
export default function QueryPage() {
  const [catalog, setCatalog] = useState<any>(null);
  const [loadingCatalog, setLoadingCatalog] = useState(true);

  // --- State Management ---
  const [appState, setAppState] = useState<AppState>({});
  const [customParsedPrompt, setCustomParsedPrompt] = useState<string | null>(null);
  const [selectedCategories, setSelectedCategories] = useState<string[]>([]);
  const categories = ["Road Sign", "Road Marking", "Traffic Calming", "Custom Query"];

  // --- AI Interaction State ---
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState<any>(null);

  // 1. Load catalog on mount
  useEffect(() => {
    fetchCatalog()
      .then((data) => {
        setCatalog(data);
      })
      .catch(console.error)
      .finally(() => {
        setLoadingCatalog(false);
      });
  }, []);

  // 2. Toggle category visibility
  const toggleCategory = (cat: string) => {
    if (selectedCategories.includes(cat)) {
      setSelectedCategories(selectedCategories.filter(c => c !== cat));
      if (cat !== "Custom Query") {
        setAppState(prev => {
          const next = { ...prev };
          delete next[cat];
          return next;
        });
      } else {
        setCustomParsedPrompt(null);
      }
    } else {
      setSelectedCategories([...selectedCategories, cat]);
    }
  };

  // 3. Handle updates from a CategoryCard
  const handleCategoryChange = (category: string, nextCategoryState: CategoryState) => {
    setAppState(prev => ({
      ...prev,
      [category]: nextCategoryState,
    }));
  };

  // 4. Handle updates from the CustomQueryBox
  const handleCustomQueryParsed = (parsed: any) => {
    const parsedData = parsed.parsed || parsed.raw;
    if (parsedData) {
      setCustomParsedPrompt(JSON.stringify(parsedData, null, 2));
    } else {
      setCustomParsedPrompt(null);
    }
  };

  // 5. Reset everything
  const restartAll = () => {
    setAppState({});
    setCustomParsedPrompt(null);
    setSelectedCategories([]);
    setResult(null);
    setIsLoading(false);
  };

  // 6. Submit to AI
  const handleSubmit = async () => {
    setIsLoading(true);
    setResult(null);
    const finalPrompt = generateLLMPrompt(appState, customParsedPrompt);
    
    try {
      const res = await sendQuery(finalPrompt);
      setResult(res); 
    } catch (e) {
      console.error(e);
      setResult({ error: "Failed to contact backend." }); 
    }
    
    setIsLoading(false);
  };

  // --- Render Logic ---

  if (loadingCatalog) {
    return (
      <>
        <Navbar />
        <LoadingView message="Loading catalog..." />
      </>
    );
  }

  if (isLoading) {
    return (
      <>
        <Navbar />
        <LoadingView />
      </>
    );
  }

  if (result) {
    return (
      <>
        <Navbar />
        <div className="w-full max-w-3xl mx-auto pt-10 px-4">
          <ResultView result={result} onRestart={restartAll} />
        </div>
      </>
    );
  }

  const canSubmit = (Object.keys(appState).length > 0 && Object.values(appState).some(cat => Object.values(cat).length > 0)) || customParsedPrompt;

  return (
    <>
      <Navbar />
      <div className="w-full max-w-3xl mx-auto pt-10 px-4 pb-20">

        {/* HEADER */}
        <header className="text-center mb-10">
          <h1 className="text-3xl font-bold">RoadMarshal AI</h1>
          <p className="text-lg font-semibold text-zinc-700 dark:text-zinc-200 mt-3">
            Hi — how can I assist you today?
          </p>
        </header>

        {/* CATEGORY SELECTION */}
        <div className="flex flex-wrap gap-3 justify-center mb-8">
          {categories.map(cat => (
            <Chip
              key={cat}
              label={cat}
              selected={selectedCategories.includes(cat)}
              onClick={() => toggleCategory(cat)}
            />
          ))}
        </div>

        {/* SHOW CARDS FOR EACH SELECTED CATEGORY */}
        <div className="space-y-8">
          
          {selectedCategories
            .filter(cat => cat !== "Custom Query")
            .map(cat => (
              <CategoryCard
                key={cat}
                id={cat} 
                catalog={catalog}
                value={appState[cat] || {}} 
                onChange={(next) => handleCategoryChange(cat, next)} 
              />
            ))}

          {selectedCategories.includes("Custom Query") && (
            <CustomQueryBox 
              onParsed={handleCustomQueryParsed} 
            />
          )}
        </div>

        {/* FINAL SUBMIT */}
        {canSubmit && (
          <div className="mt-10 border-t pt-6 flex justify-end">
            <button
              onClick={handleSubmit}
              className="px-6 py-3 bg-emerald-600 text-white rounded-lg font-semibold shadow"
            >
              Submit to AI
            </button>
          </div>
        )}

      </div>
    </>
  );
}