"use client";
import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import clsx from "clsx";
import { motion, AnimatePresence } from "framer-motion";
import type { CategoryState, TypeState, Row } from "@/app/utils/prompt";

const subgroupColors = [
  "bg-rose-100 text-rose-800",
  "bg-amber-100 text-amber-800",
  "bg-cyan-100 text-cyan-800",
  "bg-emerald-100 text-emerald-800",
];

export default function CategoryCard({
  id,
  catalog,
  value,
  onChange,
}: {
  id: string;
  catalog: any;
  value: CategoryState; // state for this category
  onChange: (next: CategoryState) => void;
}) {
  const types = catalog?.[id] ? Object.keys(catalog[id]) : [];

  // toggle a type selected/unselected
  const toggleType = (t: string) => {
    const next = { ...(value || {}) };
    if (next[t]) {
      delete next[t];
    } else {
      next[t] = { rows: [{ problems: [], speed: "", notes: "" }] };
    }
    onChange(next);
  };

  const addRow = (t: string) => {
    const next = { ...(value || {}) };
    const tx = next[t] || { rows: [] };
    tx.rows = [...tx.rows, { problems: [], speed: "", notes: "" }];
    next[t] = tx;
    onChange(next);
  };

  const removeRow = (t: string, idx: number) => {
    const next = { ...(value || {}) };
    const tx = { ...(next[t] || { rows: [] }) };
    tx.rows = tx.rows.filter((_, i) => i !== idx);
    next[t] = tx;
    onChange(next);
  };

  // toggle problem in a particular type/row
  const toggleProblem = (t: string, rowIdx: number, p: string) => {
    const next = { ...(value || {}) };
    const tx = next[t] || { rows: [] };
    const row = { ...tx.rows[rowIdx] };
    row.problems = row.problems || [];
    row.problems = row.problems.includes(p) ? row.problems.filter((x) => x !== p) : [...row.problems, p];
    tx.rows[rowIdx] = row;
    next[t] = tx;
    onChange(next);
  };

  const setField = (t: string, rowIdx: number, field: keyof Row, val: string) => {
    const next = { ...(value || {}) };
    const tx = next[t] || { rows: [] };
    const row = { ...(tx.rows[rowIdx] || { problems: [] }) };
    (row as any)[field] = val;
    tx.rows[rowIdx] = row;
    next[t] = tx;
    onChange(next);
  };

  return (
    <Card className="border">
      <CardHeader>
        <CardTitle className="text-lg">{id}</CardTitle>
      </CardHeader>

      <CardContent className="space-y-4">
        {/* Type selector as buttons (multi-select) */}
        <div>
          <div className="mb-2 text-sm font-medium">Select one or more types</div>
          <div className="flex flex-wrap gap-2">
            {types.map((t: string) => {
              const active = Boolean(value && value[t]);
              return (
                <button
                  key={t}
                  onClick={() => toggleType(t)}
                  className={clsx(
                    "px-3 py-1 rounded-full text-sm border transition-all",
                    active
                      ? "bg-blue-600 text-white border-blue-600 shadow"
                      : "bg-white dark:bg-zinc-800 border-zinc-700"
                  )}
                >
                  {t}
                </button>
              );
            })}
          </div>
        </div>

        {/* Render animated blocks for each selected type */}
        <div className="space-y-4">
          <AnimatePresence>
            {Object.keys(value || {}).map((t) => {
              const tx = value[t] as TypeState;
              const problemsList: string[] = catalog?.[id]?.[t]?.problems || [];

              return (
                <motion.div
                  key={t}
                  initial={{ opacity: 0, y: -6 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -6 }}
                  className="p-4 border rounded-lg bg-zinc-50 dark:bg-zinc-900"
                >
                  <div className="flex items-center justify-between mb-3">
                    <div className="font-semibold">{t}</div>
                    <div className="flex gap-2">
                      <button onClick={() => addRow(t)} className="px-3 py-1 bg-zinc-200 rounded">+ Row</button>
                      <button onClick={() => {
                        // unselect type
                        const next = { ...(value || {}) };
                        delete next[t];
                        onChange(next);
                      }} className="px-3 py-1 bg-zinc-100 rounded">Unselect</button>
                    </div>
                  </div>

                  {/* per-row UI */}
                  <div className="space-y-3">
                    {tx.rows.map((row, idx) => (
                      <motion.div
                        key={idx}
                        initial={{ opacity: 0, scale: 0.98 }}
                        animate={{ opacity: 1, scale: 1 }}
                        exit={{ opacity: 0, scale: 0.98 }}
                        className="p-3 border rounded"
                      >
                        <div className="text-sm text-zinc-600 mb-2">Row {idx + 1}</div>

                        {/* group chips */}
                        <div className="grid md:grid-cols-2 gap-3">
                          <div>
                            <div className="text-xs font-medium mb-1">Problems</div>
                            <div className="flex flex-wrap gap-2">
                              {problemsList.map((p: string, gi: number) => {
                                const active = (row.problems || []).includes(p);
                                return (
                                  <button
                                    key={p}
                                    onClick={() => toggleProblem(t, idx, p)}
                                    className={clsx(
                                      "px-3 py-1 rounded-full text-sm border",
                                      active ? `${subgroupColors[gi % subgroupColors.length]} border-transparent` : "bg-white dark:bg-zinc-800 border-zinc-700"
                                    )}
                                  >
                                    {p}
                                  </button>
                                );
                              })}
                            </div>
                          </div>

                          <div className="space-y-2">
                            <div>
                              <input
                                value={row.speed || ""}
                                onChange={(e) => setField(t, idx, "speed", e.target.value)}
                                placeholder="Speed (e.g., 60 km/h)"
                                className="w-full p-2 border rounded bg-transparent"
                              />
                            </div>

                            <div>
                              <input
                                value={row.notes || ""}
                                onChange={(e) => setField(t, idx, "notes", e.target.value)}
                                placeholder="Short notes (optional)"
                                className="w-full p-2 border rounded bg-transparent"
                              />
                            </div>
                          </div>
                        </div>

                        <div className="flex justify-end mt-3 gap-2">
                          <button onClick={() => removeRow(t, idx)} className="px-3 py-1 rounded bg-red-50 text-red-700">Remove row</button>
                        </div>
                      </motion.div>
                    ))}
                  </div>
                </motion.div>
              );
            })}
          </AnimatePresence>
        </div>
      </CardContent>
    </Card>
  );
}
